import global from "../constants/global";

function getComputerSelection(max) {
    let choice = Math.floor(Math.random() * 3);
    return global.choiceOptions[choice]
}
function properNoun(string) {
    return string.charAt(0).toUpperCase() + string.slice(1);
}
function choiceEmoji(name) {
    if (name === "rock") {
        return "✊"
    } else if (name === "paper") {
        return "🖐🏻"
    } else if (name === "scissors") {
        return "✌🏾"
    }
}

export default function gameReducer(state, action) {
    switch (action.type) {
        case "userSelection": {
            //get cpu selection
            let cpuChoice = getComputerSelection();
            let latestState = structuredClone(state);
            latestState.userSelection = properNoun(action.selection) + " " + choiceEmoji(action.selection);
            latestState.cpuSelection = properNoun(cpuChoice) + " " + choiceEmoji(cpuChoice);
            //compare user selection w/ cpu's selection
            if (action.selection === "rock") {
                switch (cpuChoice) {
                    case "rock": {
                        latestState.result = "Its a tie!"
                        break;
                    }
                    case "paper": {
                        latestState.result = "Paper covers rock. CPU wins!"
                        latestState.score.cpu++;
                        break;
                    }
                    case "scissors": {
                        latestState.result = "Rock breaks scissors. You win!"
                        latestState.score.user++;
                        break;
                    }
                    default: { break; }
                }
            } else if (action.selection === "paper") {
                switch (cpuChoice) {
                    case "rock": {
                        latestState.result = "Paper covers rock. You win!"
                        latestState.score.user++;
                        break;
                    }
                    case "paper": {
                        latestState.result = "Its a tie!"
                        break;
                    }
                    case "scissors": {
                        latestState.result = "Scissors cut paper. CPU wins!"
                        latestState.score.cpu++;
                        break;
                    }
                    default: { break; }
                }
            } else if (action.selection === "scissors") {
                switch (cpuChoice) {
                    case "rock": {
                        latestState.result = "Rock breaks scissors. CPU wins!"
                        latestState.score.cpu++;
                        break;
                    }
                    case "paper": {
                        latestState.result = "Scissors cut paper. You win!"
                        latestState.score.user++;
                        break;
                    }
                    case "scissors": {
                        latestState.result = "Its a tie!"
                        break;
                    }
                    default: { break; }
                }
            }
            //determine winner
            return latestState;
        }
        case "playAgain": {
            let latestState = structuredClone(state);
            latestState.userSelection = ""
            latestState.cpuSelection = ""
            latestState.result = ""
            return latestState;
        }
        case "reset": {
            //get base state
            //return base state
            let latestState = structuredClone(global.baseState);
            return latestState;
        }
        default:
            break;
    }
}