import React, { useReducer, useState } from 'react';
import './App.css';
// 1. import `ChakraProvider` component
import { ChakraProvider, Card, Box, Button, Text, Flex, CardHeader, CardBody, Heading, Center, Tabs, TabPanels, TabPanel, VStack, Image, CardFooter, HStack, ButtonGroup } from '@chakra-ui/react'
import gameReducer from "./reducers/gameReducer";
import global from './constants/global';

function App() {
  const [state, dispatch] = useReducer(gameReducer, global.baseState)
  const [tabIndex, setTabIndex] = useState(0);
  const handleTabsChange = (e) => { setTabIndex(e) };

  function handleUserSelection(e) {
    dispatch({
      type: "userSelection",
      selection: e.target.name
    })
    setTabIndex(2)
  }

  function resetGame() {
    dispatch({
      type: "reset",
    })
    setTabIndex(0)
  }

  function playAgain() {
    dispatch({
      type: "playAgain"
    })
    setTabIndex(1)
  }


  return (
    <ChakraProvider>
      <Tabs index={tabIndex} onChange={handleTabsChange}>
        <TabPanels>
          <TabPanel>
            <Center h={"100vh"}>
              <VStack spacing={10}>
                <Box>
                  <Image src='imgs/rpsImage.png' />
                </Box>
                <Box boxShadow={"sm"}>
                  <Button mt={5} boxShadow={"dark-lg"} onClick={() => handleTabsChange(1)} colorScheme={"twitter"}>
                    Start Game
                  </Button>
                </Box>
              </VStack>
            </Center>
          </TabPanel>
          <TabPanel>
            <Center h="100vh">
              <VStack>
                <Text>Shoot Your Shot...</Text>
                <Flex justifyContent={"center"} alignItems={"center"}>
                  <Card boxShadow='lg' m="2" rounded='md'>
                    <CardHeader>
                      <Heading size='md' textAlign="center">Rock</Heading>
                    </CardHeader>
                    <CardBody>
                      <Center fontSize={40}>
                        ✊
                      </Center>
                    </CardBody>
                    <CardFooter >
                      <Button onClick={handleUserSelection} name='rock' py={"25px"} shadow="dark-lg" colorScheme='twitter' >
                        Choose<br />Rock
                      </Button>
                    </CardFooter>
                  </Card>
                  <Card boxShadow='lg' m="2" rounded='md'>
                    <CardHeader>
                      <Heading size='md' textAlign="center">Paper</Heading>
                    </CardHeader>
                    <CardBody>
                      <Center fontSize={40}>
                        🖐🏻
                      </Center>
                    </CardBody>
                    <CardFooter >
                      <Button onClick={handleUserSelection} name="paper" py={"25px"} shadow="dark-lg" colorScheme='twitter'>
                        Choose<br />Paper
                      </Button>
                    </CardFooter>
                  </Card>
                  <Card boxShadow='lg' m="2" rounded='md'>
                    <CardHeader>
                      <Heading size='md' textAlign="center">Scissors</Heading>
                    </CardHeader>
                    <CardBody>
                      <Center fontSize={40}>
                        ✌🏾
                      </Center>
                    </CardBody>
                    <CardFooter >
                      <Button onClick={handleUserSelection} name='scissors' py={"25px"} shadow="dark-lg" colorScheme='twitter'>
                        Choose<br />Scissors
                      </Button>
                    </CardFooter>
                  </Card>
                </Flex>
              </VStack>
            </Center>
          </TabPanel>
          <TabPanel>
            <Center h="100vh">
              <VStack>
                <Box mb="50">
                  <Heading>{state.result}</Heading>
                </Box>
                <HStack spacing={10}>
                  <Box>
                    <Text><em><strong>You chose:</strong></em> {state.userSelection}</Text>
                  </Box>
                  <Box>
                    <Text><em><strong>CPU chose:</strong></em> {state.cpuSelection}</Text>
                  </Box>
                </HStack>
                <ButtonGroup m={5}>
                  <Button onClick={resetGame} colorScheme='red'>Reset Game</Button>
                  <Button onClick={playAgain} colorScheme='twitter'>Play Again</Button>
                </ButtonGroup>
                <Box textAlign={"center"}>
                  <strong>Score</strong><br/>
                  You - {state.score.user} | CPU - {state.score.cpu}
                </Box>
              </VStack>
            </Center>
          </TabPanel>
        </TabPanels>
      </Tabs>
    </ChakraProvider >
  )
}

export default App;



